.slider-movies
    height: 60vh
    &__movie
        height: 60vh
        background-size: cover
        background-repeat: no-repeat
        background-position: center
        &-info
            display: flex
            align-items: flex-end
            height: 100%
            padding-bottom: 40px
            > div
                background-color: #a7a7a7cf
                margin: 0 auto 
                text-align: center
                width: 50%
                padding: 20px
            